import Link from "next/link";
import { useState } from "react";

const Menu = () => {
  const [toggle, setToggle] = useState(false);

  const menuStyle = e => {
    if (!e) {
      return {
        top: "3rem",
        left: "3rem",
        transition: ".5s"
      };
    } else {
      return {
        top: 0,
        left: 0,
        transition: ".5s"
      };
    }
  };

  const menuToggle = e => {
    if (!e) {
      setToggle(true);
    } else setToggle(false);
  };

  return (
    <nav className="menu" style={menuStyle(toggle)}>
      <button className="toggle" onClick={() => menuToggle(toggle)}></button>
      <Link href="/">
        <a>Home</a>
      </Link>
      <Link href="/frontend">
        <a>Frontend</a>
      </Link>
      <Link href="/design">
        <a>Design</a>
      </Link>
      <Link href="/resume">
        <a>Resum√©</a>
      </Link>
      <style jsx>
        {`
          .menu {
            display: flex;
            flex-direction: column;
            width: auto;
            height: 100%;
          }

          .toggle {
            display: none;
            position: absolute;
            height: 100vh;
            width: 100vw;
            background-color: transparent;
            z-index: 1;
          }

          nav a {
            color: black;
            padding: 0.25rem;
            font-size: 1rem;
            transition: color 0.35s;
            margin-left: 0;
            z-index: 2;
          }

          nav a:nth-child(n + 2) {
            margin-top: 0.5rem;
          }

          nav a:focus {
            background-color: black;
            color: white;
            border: solid 3px transparent;
          }

          @media only screen and (max-width: 1024px) {
            .menu {
              position: absolute;
              width: 110vw;
              height: 100vh;
              background-color: black;
              justify-content: center;
              align-items: center;
            }

            .toggle {
              display: block;
            }

            nav a {
              text-align: center;
              width: 50%;
            }

            nav a:nth-child (1) {
              margin-top: 1rem;
            }
          }

          nav a:hover {
            background-color: black;
            color: transparent;
            transition: color 0.35s;
          }

          @media only screen and (orientation: portrait) {
            nav a {
              padding: 1.5rem 0.5rem;
            }
          }

          @media only screen and (max-width: 1024px) {
            nav a:nth-child (1) {
              margin-top: 0.5rem;
            }
          }
        `}
      </style>
    </nav>
  );
};

export default Menu;
